import React from "react";
import styled from "styled-components";
import useAxios from "../api/Axios";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useAuth } from "../Context/Auth";
import { useDispatch } from "react-redux";
import { setNavBarTitle } from "../reduxjs/TitleSlice";
import { handleError } from "../utils/Toastify";
import ClassInformation from "../components/assets/ClassInformation";

const Achieved = () => {
  const axios = useAxios();
  const navigate = useNavigate();
  const location = useLocation();
  const auth = useAuth();
  const dispatch = useDispatch();
  const [classInfo, setClassInfo] = React.useState([]);

  const param = useParams();

  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    setLoading(true);
    auth.handleDashboard(true);
    auth.handleSetTitle("Achieved Class");
    dispatch(setNavBarTitle("Achieved Class"));
    getClass();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const getClass = async () => {
    try {
      const url = "/class/all?id=" + param.tid + "&achieved=1";
      await axios
        .get(url)
        .then((res) => {
          setClassInfo(res.data);
        })
        .catch((err) => {
          handleError(err?.reaponse?.data?.message);
        });
    } catch (error) {
      handleError(error?.reaponse?.data?.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Section>
      {loading ? (
        <Container>
          <ClassPanel>
            <Loading />
            <Loading />
            <Loading />
          </ClassPanel>
        </Container>
      ) : (
        <Container>
          <Container>
            {classInfo.length > 0 ? (
              <ClassPanel>
                {classInfo.map((data) => {
                  return (
                    <Classes
                      key={data.id}
                      // onClick={() => handleClickedClass(data.classId)}
                    >
                      <ClassInformation classInfo={data} onReload={getClass} />
                    </Classes>
                  );
                })}
              </ClassPanel>
            ) : (
              <ClassPanel style={{ display: "flex" }}>
                <Panel>
                  <Image
                    src={require("../components/images/empty-box.png")}
                    alt="No-Record"
                  />
                  <Text>No Class Found.</Text>
                  <Text>
                    Go Back Dashboard. Then Achieved Class which you want.
                  </Text>
                  <Button
                    onClick={() => {
                      const path = location.pathname.replace(
                        "/achieved",
                        "/dashboard"
                      );
                      navigate(path);
                    }}
                  >
                    Dashboard
                  </Button>
                </Panel>
              </ClassPanel>
            )}
          </Container>
        </Container>
      )}
    </Section>
  );
};

export default Achieved;

const Loading = () => {
  return (
    <LoadingDiv
      barient="rgba(250,250,250,1)"
      style={{ border: "1px solid rgba(3,3,3,0.1)" }}
    >
      <Div
        style={{
          width: "60%",
        }}
      />
      <Div
        style={{
          width: "40%",
        }}
      />
      <Div
        style={{
          width: "30%",
        }}
      />
      <Div
        style={{
          width: "50%",
        }}
      />
      <Div2 />
    </LoadingDiv>
  );
};

const Section = styled.section`
  margin-top: 5px;
  min-height: calc(100vh - 110px);
  overflow-y: visible !important;
`;

const Container = styled.div`
  padding: 0.5rem;
`;

const LoadingDiv = styled.div`
  margin-bottom: 0.5rem;
  width: 491px;
  margin-right: 5px;
  margin-bottom: 5px;
  padding: 0.5rem;
  position: relative;
  height: 150px;
  border-radius: 5px;
  background: rgba(240, 240, 240, 1);

  @media (max-width: 992px) {
    width: 100%;
    &:nth-child(2n + 1) {
      margin-right: 0px;
    }
    &:nth-child(2n + 2) {
      margin-left: 0px;
    }
  }
`;

const Classes = styled.div`
  cursor: pointer;
  width: 98%;
  margin: 1%;

  @media (max-width: 768px) {
    width: 100%;
    margin: 1% 0px;
  }
`;

const Panel = styled.div`
  height: 63.5vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const ClassPanel = styled.div`
  display: grid;
  flex-wrap: wrap;
  grid-template-columns: 33% 33% 33%;

  @media (max-width: 992px) {
    grid-template-columns: 50% 50%;
  }
  @media (max-width: 768px) {
    display: flex;
    flex-direction: column;
  }
`;

const Button = styled.button`
  width: 120px;
  background: green;
  color: #fff;
  font-weight: 600;
  border-radius: 5px;
`;

const Text = styled.p`
  text-align: center;
  &:nth-child(2) {
    font-weight: 600;
    font-size: 1.2rem;
    margin-bottom: 0;
  }
`;

const Image = styled.img`
  width: 220px;
  opacity: 0.5;

  @media (max-width: 768px) {
    width: 170px;
  }
`;

const Div = styled.div`
  background: rgba(210, 210, 210, 1);
  border-radius: 50px;
  height: 20px;
  margin-bottom: 7px;

  &:nth-child(1) {
    margin-bottom: 20px;
  }
  animation-name: breath-animation;
  animation-duration: 1s;
  animation-iteration-count: infinite;

  @keyframes breath-animation {
    0% {
      opacity: 0.9;
    }
    25% {
      opacity: 0.6;
    }
    50% {
      opacity: 0.4;
    }
    75% {
      opacity: 0.6;
    }
    100% {
      opacity: 0.9;
    }
  }
`;

const Div2 = styled.div`
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  background: rgba(210, 210, 210, 1);
  top: 13px;
  right: 10px;
  animation-name: breath-animation;
  animation-duration: 1s;
  animation-iteration-count: infinite;

  @keyframes breath-animation {
    0% {
      opacity: 0.9;
    }
    25% {
      opacity: 0.6;
    }
    50% {
      opacity: 0.4;
    }
    75% {
      opacity: 0.6;
    }
    100% {
      opacity: 0.9;
    }
  }
`;
